import React, { Component } from 'react'
import { Link } from 'gatsby'
import Img from 'gatsby-image'

import { H2, Button } from '@style/global'
import styled from 'styled-components'
import theme from '@style/theme'

class Preview extends Component {
  render() {
    const listCategory = this.props.category.map((item, i) => {
      return <LsCat key={i}>{item}</LsCat>
    })
    const listRole = this.props.role.map((item, i) => {
      return <LsRole key={i}>{item}</LsRole>
    })
    return (
      <PreviewBloc>
        <Info>
          <CategoryDate>
            {listCategory}
            <time dateTime={this.props.date}>{this.props.date}</time>
          </CategoryDate>
          <H2>{this.props.title}</H2>
          <Desc>{this.props.desc}</Desc>
          <hr />
          <p>
            <b>Roles</b>
            <br />
            {listRole}
          </p>
          <BtnGroup>
            {this.props.visit ? (
              <Link to={this.props.visit} target="blank">
                <Button primary>Visit Work</Button>
              </Link>
            ) : (
              ''
            )}
            {this.props.hideStory ? (
              ''
            ) : (
              <Link to={this.props.path}>
                <Button>Read Story</Button>
              </Link>
            )}
          </BtnGroup>
        </Info>
        <ImgSlide>
          {this.props.img ? <Img fluid={this.props.img} /> : ''}
        </ImgSlide>
      </PreviewBloc>
    )
  }
}

export default Preview

const PreviewBloc = styled.div`
  display: grid;
  height: 100% !important;
  grid-template-columns: 1fr 2fr;
  padding: 0 !important;
  margin: 0 !important;
  @media (max-width: ${theme.screen.lg}) {
    grid-template-columns: unset !important;
    height: auto !important;
  }
`

const Info = styled.div`
  padding: 60px 40px;
  height: 100% !important;
  overflow: scroll;
  background: ${theme.color.white.regular};
  @media (max-width: ${theme.screen.lg}) {
    padding: 25px 16px 0 16px;
  }
`

const ImgSlide = styled.div`
  background: ${theme.color.secondary.S900};
  @media (max-width: ${theme.screen.lg}) {
    order: -1;
    height: 200px;
    object-fit: cover;
  }
  div {
    height: 100% !important;
  }
  img {
    object-fit: cover;
    object-position: 50% 50%;
  }
`

const CategoryDate = styled.p`
  color: ${theme.color.grayscale.S600};
  margin-bottom: 20px;
  ${theme.fontSize.xs};
  @media (max-width: ${theme.screen.lg}) {
    margin-bottom: 10px;
  }
`

const LsCat = styled.span`
  ::after {
    content: ',   ';
  }
  :last-of-type {
    ::after {
      content: '・';
    }
  }
`

const Desc = styled.p`
  @media (max-width: ${theme.screen.lg}) {
    ${theme.fontSize.sm};
  }
`

const LsRole = styled.div`
  @media (max-width: ${theme.screen.lg}) {
    ${theme.fontSize.sm};
  }
  ::before {
    content: '- ';
  }
`

const BtnGroup = styled.div`
  margin: 45px 0;
  @media (max-width: ${theme.screen.lg}) {
    margin: 25px 0;
  }
  div {
    margin-left: 10px;
  }
  div:first-of-type {
    margin-left: 0px !important;
  }
`
